                <a :href="s3Info.s3FileHeadInfo.url">click here</a>
                to download.
              </span>
              <span v-else>
                Previously generated report was on
                {{ s3Info.s3FileHeadInfo.LastModified | formatDateTime }},
                <a :href="s3Info.s3FileHeadInfo.url">click here</a>
                to download.
              </span>
            </v-card-text>
          </div>
          <div>
