<script>
import { useReportDownloadWindow } from "@/modules";

export default {
  props: {
    value: {
      type: Boolean
    },
    title: {
      type: String
    },
    api: {
      type: String
    },
    s3Info: {
      type: Object,
      default: null
    },
    checkIfGenerationCompleted: {
      type: Function
    }
  },
  setup: useReportDownloadWindow
